import _ from "lodash";
import React from "react";
import PropTypes from "prop-types";
import { withPropsOnChange } from "recompose";
import styled from "styled-components";

const replies = ["申請前已對外提供", "同意提供", "不同意提供", "還未回覆"];

const nameColors = ["#aab6fe", "#b2fab4", "#ffa4a2", "#ffe97d"];
const valueColors = ["#49599a", "#519657", "#af4448", "#c88719"];

const Wrapper = styled.div`
  width: 100%;
  margin: 0 auto;
  flex: 1;
  display: flex;
  flex-direction: row;
`;

const KindName = styled.div`
  font-size: 1.5em;
  flex: 1;
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
  padding: 0 20px 20px;
  color: ${({ idx }) => nameColors[idx]};
`;

const Bar = styled.div`
  position: relative;
  height: 100%;
  width: 100%;
  max-width: 600px;
  display: flex;
  flex-direction: row-reverse;
  align-items: flex-end;
`;

const Item = styled.div`
  position: relative;
  flex: 1;
  background-color: rgba(255, 255, 255, 0.6);
  margin: 0 5px;
  height: ${({ value }) => value * 500}%;
  transition-property: height;
  transition-duration: 1s;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  padding-bottom: 20px;
  font-size: 1.2em;
  color: ${({ idx }) => valueColors[idx]};
`;

const Organizer = styled.div`
  position: absolute;
  top: -30px;
  right: 0;
  text-align: right;
  width: 200px;
  color: #ffffff;
`;

export default class Collection extends React.PureComponent {
  static propTypes = {
    data: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
  };

  render() {
    const { data, ...props } = this.props;

    return (
      <Wrapper {...props}>
        {_.map(data, (item, idx) => (
          <div key={`${idx}-${item["原ID"]}`}>{item["原ID"]}</div>
        ))}
      </Wrapper>
    );
  }
}
